<template>
  <div class="app-container">
    <div class="filter-container">
      <el-button @click="handleShow('','','insert')">新增员工信息</el-button>
    </div>
    <el-table :data="list" v-loading.body="listLoading" element-loading-text="拼命加载中" border fit highlight-current-row>
      <el-table-column align="center" label='ID' width="95">
        <template scope="scope">
          {{scope.$index}}
        </template>
      </el-table-column>
      <el-table-column label="员工ID">
        <template scope="scope">
          {{scope.row.employeeId}}
        </template>
      </el-table-column>
      <el-table-column label="员工姓名" align="center">
        <template scope="scope">
          <span>{{scope.row.employeeName}}</span>
        </template>
      </el-table-column>
      <el-table-column label="性别" align="center">
        <template scope="scope">
          <span>{{scope.row.gender}}</span>
        </template>
      </el-table-column>
      <el-table-column label="工龄(年)" align="center">
        <template scope="scope">
          <span>{{scope.row.workingYears}}</span>
        </template>
      </el-table-column>
      <el-table-column label="职位" align="center">
        <template scope="scope">
          <span>{{scope.row.duty}}</span>
        </template>
      </el-table-column>
      <el-table-column align="center" label="操作">
        <template scope="scope">
          <el-button size="small" @click="handleShow(scope.$index, scope.row, 'show')">查看</el-button>
          <el-button size="small" type="primary" @click="handleShow(scope.$index, scope.row, 'update')">编辑</el-button>
          <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="block">

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage6"
        :page-size="pages.pageSize"
        :page-sizes="[10, 20, 30, 40]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pages.total">
      </el-pagination>
    </div>

    <!-- Form -->

    <el-dialog title="新增员工信息" :visible.sync="dialogFormVisible">
      <el-form :model="form">
        <el-form-item label="员工姓名" :label-width="formLabelWidth">
          <el-input v-model="form.employeeName" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="性别" :label-width="formLabelWidth">
          <el-input v-model="form.gender" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="工龄(年)" :label-width="formLabelWidth">
          <el-input v-model="form.workingYears" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="职位" :label-width="formLabelWidth">
          <el-input v-model="form.duty" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="入职时间" :label-width="formLabelWidth">
          <el-input v-model="form.hireTime" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="handleSure()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import {getEmployeeList, getEmployeeInfo, insertEmployee, updateEmployee, deleteEmployee} from '@/api/basic'

  export default {
    data()
    {
      return {
        list: null,
        listLoading: true,
        state: 'insert',
        form: {
          employeeName: ''
        },
        params: {
          employeeId: ''
        },
        pages: {
          total: '',
          pageNum: 1,
          pageSize: 10
        },
        dialogFormVisible: false
      }
    },
    created()
    {
      this.fetchData()
    },
    methods: {
      fetchData()
      {
        this.listLoading = true
        getEmployeeList(this.pages).then(response =>
        {
          console.log(response);
          this.pages.total = response.data.totalPage
          this.list = response.data.result
          this.listLoading = false
        })
      },
      insertEmployee()
      {
        return new Promise((resolve, reject) =>
        {
          insertEmployee(this.form).then(response =>
          {
            this.dialogFormVisible = false
            resolve()
          }).catch(error =>
          {
            reject(error)
          })
        })
      },
      updateEmployee()
      {
        return new Promise((resolve, reject) =>
        {
          updateEmployee(this.form).then(response =>
          {
            this.dialogFormVisible = false
            resolve()
          }).catch(error =>
          {
            reject(error.msg)
          })
        })
      },
      deleteEmployee()
      {
        return new Promise((resolve, reject) =>
        {
          deleteEmployee(this.params).then(response =>
          {
            this.dialogFormVisible = false
            resolve()
          }).catch(error =>
          {
            reject(error.msg)
          })
        })
      },
      getEmployeeInfo()
      {
        return new Promise((resolve, reject) =>
        {
          getEmployeeInfo(this.params).then(response =>
          {
            this.dialogFormVisible = false
            resolve()
          }).catch(error =>
          {
            reject(error.msg)
          })
        })
      },
      handleShow(index, row, state)
      {
        this.dialogFormVisible = true
        if (state == 'insert')
        {
          this.state = 'insert';

        }
        else if (state == 'update')
        {
          this.form.employeeId = row.employeeId;
          this.form.employeeName = row.employeeName;
          this.state = 'update';
        }
        else if (state == 'show')
        {
          this.form = row;
          this.state = 'show';
        }
      },
      handleSure()
      {
        if (this.state == 'insert')
        {
          insertEmployee(this.form);
        }
        else if (this.state == 'update')
        {
          updateEmployee(this.form);
        }
        else if (this.state == 'show')
        {
          getEmployeeInfo(this.form);
        }
      },
      handleDelete(index, row)
      {
        this.$confirm('确认删除？')
          .then(_ =>
          {
            this.params.employeeId = row.employeeId;
            deleteEmployee(this.params);
            done();
          })
          .catch(_ =>
          {

          });
      },
      handleSizeChange(size)
      {
        this.pages.pageSize = size
        this.fetchData();
      },
      handleCurrentChange(currentPage)
      {
        this.pages.pageNum = currentPage
        this.fetchData();
      }
    }
  }
</script>



